import React from 'react'
import { Tree, useTreeState } from 'react-hyper-tree'
import BaseLayout from '../../components/BaseLayout';
import { smallData } from './data'

const HyperTree = () => {
  const { required, handlers } = useTreeState(
    { data: smallData, id: 'treeView', defaultOpened: true, multipleSelect: false },
  )
  return (
    <BaseLayout
      className="example-hyper-tree-wrapper"
      title="react hyper tree"
      link="https://github.com/alekseymakhankov/hyper-tree"
    >
      <Tree
        {...required}
        {...handlers}
        depthGap={20}
        horizontalLineStyles={{
          stroke: 'black',
          strokeWidth: 1,
          strokeDasharray: '1 4',
        }}
        verticalLineStyles={{
          stroke: 'black',
          strokeWidth: 1,
          strokeDasharray: '1 4',
        }}
        gapMode="padding"
      />
    </BaseLayout>
  )
}

export default HyperTree
